<template>
  <div class="xslider">
    <div class="xslider-view">
      <div class="xslider-slide" v-for="(item, index) in list" :key="index">
        <div class="xslider-layer-gl" :style="'background:url('+item+');background-size:cover;'"></div>
      </div>
    </div>
    <div class="xslider-pager"></div>
    <div class="xslider-prev"></div>
    <div class="xslider-next"></div>
  </div>
</template>


<script>
import XSlider from "xslider";
import Swiper from "swiper";
export default {
  props: {
    list: Array,
    transition: Object
  },
  mounted() {
    this.main = new XSlider(".xslider", { transition: this.transition });
    // this.main = new XSlider(".xslider");
  }
};
</script>

